import React, { useEffect, useMemo, useState } from 'react';
import {
  Input,
  Row,
  Col,
  Button,
  Icon,
} from 'antd';
import { useLocation } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import headClasses from './header.module.less';
import DownloadButton from './components/download-button/DownloadButton';
import { search as storeSearch } from '../../store/search/action';
import { logout } from '../../store/admin/action';
import paths from '../../constants/index';


const { Search } = Input;


function HeaderTop({ search, searchAction, logoutAction, onClickHamburger, menuOpen }) {
  const { pathname } = useLocation();
  const showSearch = useMemo(() => (pathname !== `/${paths.DELIVERY_TIME}`
      && pathname !== `/${paths.SUPPORT}`
      && pathname !== `/${paths.MAIN_PAGE}`
      && pathname !== `/${paths.MAIN_SLIDES}`), [pathname]);
  const [value, setValue] = useState('');
  useEffect(() => setValue(search.searchWord), [search.searchWord]);
  const handleChange = ({ target }) => setValue(target.value);
  return (
    <div className={headClasses.HeadTop}>
      <Row>

        <Col lg={15} style={{ textAlign: 'right', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <div style={{ textAlign: 'start' }}>
          {menuOpen ? (
            <svg onClick={onClickHamburger} width="30px" height="30px" viewBox="0 0 50 50" enableBackground="new 0 0 50 50" fill="black" style={{ cursor: 'pointer' }}>
              <path fill="black" d="M8.667,15h30c0.552,0,1-0.447,1-1s-0.448-1-1-1h-30c-0.552,0-1,0.447-1,1S8.114,15,8.667,15z"/>
              <path fill="black" d="M8.667,37h30c0.552,0,1-0.447,1-1s-0.448-1-1-1h-30c-0.552,0-1,0.447-1,1S8.114,37,8.667,37z"/>
              <path fill="black" d="M8.667,26h30c0.552,0,1-0.447,1-1s-0.448-1-1-1h-30c-0.552,0-1,0.447-1,1S8.114,26,8.667,26z"/>
            </svg>) : (
              <svg onClick={onClickHamburger} width="24" height="24" viewBox="0 0 24 24" fill="black" xmlns="http://www.w3.org/2000/svg" style={{ cursor: 'pointer' }}>
                <rect x="2" width="30.9785" height="2" transform="rotate(45 2 0)" fill="black"/>
                <rect y="21.9053" width="30.9785" height="2" transform="rotate(-45 0 21.9053)" fill="black"/>
              </svg>
          )}
          </div>
          <DownloadButton pathname={pathname} />
        </Col>
        <Col lg={7}>
          {showSearch && (
          <Search
            placeholder="Search"
            onSearch={(value) => searchAction({ value, pathname: pathname.replace(/\//g, '') })}
            enterButton
            loading={search.loading}
            value={value}
            onChange={handleChange}
          />
          )}
        </Col>
        <Col lg={2}>
          <Button className={headClasses.btnElq} type="link" onClick={logoutAction}>
            Ելք
            <Icon type="caret-right" />
          </Button>
        </Col>
      </Row>
    </div>
  );
}

const mapStateToProps = ({ search }) => ({ search });

HeaderTop.propTypes = {
  search: PropTypes.objectOf(Object).isRequired,
  logoutAction: PropTypes.func.isRequired,
  searchAction: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, {
  searchAction: storeSearch,
  logoutAction: logout,
})(HeaderTop);
