import { handleActions } from 'redux-actions';
import { downloadCustomersRequested, downloadCustomersSuccess, downloadCustomersError } from './action';

const initialState = {
  downloadStatus: {
    inProcess: false,
    error: false,
  },
};

export default handleActions({
  [downloadCustomersRequested]: (state) => ({
    ...state,
    downloadStatus: {
      inProcess: true,
      error: false,
    },
  }),
  [downloadCustomersSuccess]: (state) => ({
    ...state,
    downloadStatus: {
      inProcess: false,
      error: false,
    },
  }),
  [downloadCustomersError]: (state) => ({
    ...state,
    downloadStatus: {
      inProcess: false,
      error: true,
    },
  }),
}, initialState);

export const selectDownloadStatus = ({ customersDownload }) => customersDownload.downloadStatus;
